:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.container {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img__logo {
  width: 40px;
  height: 40px;
}

.nav {
  align-self: center;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--main-color);
  margin: 0;
  position: relative;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

